@import "../../global.scss";

.intro{
    background-color: rgb(5, 5, 5);
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left {
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 100px;
            height: 700px;
            background-color: rgb(14, 13, 13);
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile{
                align-items: start;
             }

            img{
                height: 90%;

                @include mobile{
                   height: 50%;
                }

            }
        }
    }

    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
            }


            h1{
                font-size: 60px;
                margin: 10px 0;
                color: whitesmoke;

                @include mobile{
                font-size: 40px;
                }
   
            }
            h2{
              font-size: 35px;  
              color: whitesmoke;
            }
            h3{
                font-size: 30px;
                color: whitesmoke;
                @include mobile{
                  font-size: 20px;
                }
   

                span{
                    font-size: inherit;
                    color: crimson;
                }

                .ityped-cursor{
                    animation: blink 1s infinite;
                    color: whitesmoke;
                }                                               

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 40%;

            img{
                width: 50px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink{
                100%{
                   opacity: 0; 
                }
        

                
                
            }
        }
    }
}